.alc-modal__overlay {
    /* Posicionamento */
    @apply z-10 w-screen h-screen top-0 left-0 p-2 fixed;
    /* Alinhamento do elemento interno (cartão) */
    @apply flex items-center justify-center;
}
  
.alc-modal__card {
    /* Aparência */
    @apply bg-white border-2;
    /* Tamanho */
    @apply w-full md:max-w-lg;
}

.alc-modal__header {
    @apply flex items-center justify-between;
}

.alc-modal__close {
    /* Posicionamento e tamanho */
    @apply text-lg w-8 h-8;
}